import SVG from "react-inlinesvg";

import iconHome from "../../Assets/images/icon-home.svg";
import iconBalance from "../../Assets/images/icon-balance.svg";
import iconContactuse from "../../Assets/images/icon-contactus.svg";
import iconStatement from "../../Assets/images/icon-statement.svg";
import iconAccount from "../../Assets/images/icon-account.svg";
import { useState } from "react";
import { shallowEqual, useSelector } from "react-redux";
import Statement from "../Statement";
import Account from "../Account";
import { useNavigate } from "react-router-dom";

const Footer = () => {
  const { stateAuth } = useSelector(
    (state: any) => ({ stateAuth: state.auth.user }),
    shallowEqual
  );
  
  const navigate = useNavigate()
  const handleChangeTab = (select: string) => {
    setSelected(select);
  }
  const openBalance = () => {
    setBalance(!balance)
  }

  const [selected, setSelected] = useState('home')
  const [balance, setBalance] = useState(false)
  const [contactus, setContacus] = useState(false);
  const [statement, setStatement] = useState(false);
  const [account, setAccount] = useState(false);
  return <>
    <div className="c-footer">
      <div className="c-tabbar c-tabbar--svg">
        <div className="c-tabbar__item" data-selected={selected === 'home' ? 'true' : 'false'} onClick={() => {
          handleChangeTab('home')
          setStatement(false);
          setAccount(false);
          setContacus(false);
          navigate('/lobby')
        }}>
          <div className="c-tabbar__btn"><i className="c-iconsvg-home">
            {/* icon home */}
            <SVG src={iconHome} />
          </i>
            <div className="c-tabbar__text">Trang Ch</div>
          </div>
        </div>
        <div className="c-tabbar__item" data-selected={balance ? 'true' : 'false'} onClick={() => {
          // handleChangeTab('balance')
          openBalance();
        }}>
          <div className="c-tabbar__btn"><i className="c-iconsvg-balance"><SVG src={iconBalance} /></i>
            <div className="c-tabbar__text">S D</div>
          </div>
        </div>
        <div className="c-tabbar__item" data-selected={selected === 'contactus' ? 'true' : 'false'} onClick={() => {
          handleChangeTab('contactus')
          setStatement(false);
          setAccount(false);
          setContacus(true);
        }}>
          <div className="c-tabbar__btn"><i className="c-iconsvg-contactus"><SVG src={iconContactuse} /></i>
            <div className="c-tabbar__text">Lin h</div>
          </div>
        </div>
        <div className="c-tabbar__item" data-selected={selected === 'statement' ? 'true' : 'false'} onClick={() => {
          handleChangeTab('statement')
          setStatement(true);
          setContacus(false);
          setAccount(false);
        }}>
          <div className="c-tabbar__btn"><i className="c-iconsvg-statement"><SVG src={iconStatement} /></i>
            <div className="c-tabbar__text">Sao k</div>
          </div>
        </div>
        <div className="c-tabbar__item" data-selected={selected === 'account' ? 'true' : 'false'} onClick={() => {
          handleChangeTab('account')
          setStatement(false);
          setContacus(false);
          setAccount(true);
        }}>
          <div className="c-tabbar__btn"><i className="c-iconsvg-account"><SVG src={iconAccount} /></i>
            <div className="c-tabbar__text">Ti</div>
          </div>
        </div>
      </div>
      <div className="c-gc-balance" data-open={balance ? 'true' : 'false'}>
        <div className="c-gc-balance__account"><i className="c-gc-icon c-gc-icon--account-circle"></i>
          <div className="c-gc-balance__account-text"><span className="c-gc-text">{stateAuth.username}</span><span className="c-gc-text">Loi
            tin t:UT</span></div>
        </div>
        <div className="c-gc-balance__wallet">
          <div className="c-gc-balance__group">
            <div className="c-gc-balance__item"><span className="c-gc-text-label">S d</span><span
              className="c-gc-text-value">{stateAuth.profit ?? 0.00}</span></div>
            <div className="c-gc-balance__item"><span className="c-gc-text-label">Hn mc cn li</span><span
              className="c-gc-text-value">{stateAuth.wallet ?? 0.00}</span></div>
          </div>
          <div className="c-gc-balance__item"><span className="c-gc-text-label">Cha x l</span><span
            className="c-gc-text-value">{stateAuth.runing_amount_today ?? 0.00}</span></div>
          <div className="c-gc-balance__item" style={{ display: 'none' }}><span className="c-gc-text-label">Gi tr rng</span><span
            className="c-gc-text-value">0.00</span></div>
          <div className="c-gc-balance__item"><span className="c-gc-text-label">Hn mc c cp</span><span
            className="c-gc-text-value">{stateAuth.credit_line ?? 0.00}</span></div>
          {/* <div className="c-gc-balance__item"><span className="c-gc-text-label">Tin thng cha x
            l</span><span className="c-gc-text-value">{stateAuth.runing_amount_not_today ?? 0.00}</span></div> */}
        </div>
      </div>
      <div className="c-overlay" onClick={openBalance}></div>
    </div>
    <div id="overlayComponents" style={{ display: !contactus ? 'none' : 'block' }}>
      <div id="pagePanel" className="contactUsPanel">
        <div className="page page-contactus" data-status="is-open">
          <div className="page_header">
            <div className="main-bar">
              <div className="title">Lin h</div>
            </div>
          </div>
          <div className="page_content">
            <div className="content_note">Chuyn vin Dch v Khch hng lun sn sng tr li mi cu hi ca bn lin quan n
              tin thanh ton hoc vn  khc bng Ting Anh v Ting Trung.</div>
            <div className="content_name">Lin h</div>
            <div className="content_group">
              <div className="content_row touch">
                <div className="col_title">
                  <div className="title_name"><i className="icon icon-letstalk"></i>Letstalk<div className="title_tag">For Better
                    Security</div>
                  </div>
                  <div className="title_value">viva88</div>
                </div>
                <div className="col_value"><i
                  className="icon icon-chat text-active"></i></div>
              </div>
              <div className="content_row touch">
                <div className="col_title">
                  <div className="title_name"><i className="icon icon-live-chat"></i>Live Chat</div>
                  <div className="title_value"></div>
                </div>
                <div className="col_value"><a><i className="icon icon-chat text-active"></i></a></div>
              </div>
              <div className="content_row touch">
                <div className="col_title">
                  <div className="title_name"><i className="icon icon-skype"></i>Skype</div>
                  <div className="title_value">VIVA88.csd</div>
                </div>
                <div className="col_value"><a href="https://join.skype.com/invite/kxq7bs7dKM1B"><i
                  className="icon icon-chat text-active"></i></a></div>
              </div>
              <div className="content_row touch">
                <div className="col_title">
                  <div className="title_name"><i className="icon icon-zalo"></i>Zalo</div>
                  <div className="title_value">Viva88csd</div>
                </div>
              </div>
            </div>
            <div className="content_name">Gi email cho chng ti</div>
            <div className="content_group">
              <div className="content_row touch">
                <div className="col_title">
                  <div className="title_name"><i className="icon icon-email"></i>Email</div>
                </div>
              </div>
              <div className="content_row touch">
                <div className="col_title">
                  <div className="title_value">csd@viva88.com</div>
                </div>
                <div className="col_value"><a href="mailto:csd@viva88.com"><i className="icon icon-email text-active"></i></a></div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    {selected === 'statement' && <Statement open={statement} />}
    <Account open={account} />
  </>
}

export default Footer;